import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Container, Typography, TextField, Button, Grid, Card, CardContent, CardMedia } from '@mui/material';

function App() {
  const [news, setNews] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');

  useEffect(() => {
    const fetchNews = async () => {
      try {
        const response = await axios.get('https://newsapi.org/v2/everything?domains=cnn.gr,metropolis.gr,news247.com,naftemporiki.gr,protothema.gr,newsit.gr&apiKey=402e6a4e9f7a4a4eb990fa57da03604e');
        setNews(response.data.articles);
      } catch (error) {
        console.error('Error fetching news:', error);
      }
    };

    fetchNews();
  }, []);

  const handleArticleClick = (url) => {
    window.open(url, '_blank');
  };

  const handleSearchChange = (event) => {
    setSearchTerm(event.target.value);
  };

  const filteredNews = news.filter((article) =>
    article.title.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <Container maxWidth="lg" style={{ marginTop: '20px' }}>
      <Typography variant="h4" align="center" gutterBottom>Kearch News</Typography>
      <div style={{ marginBottom: '20px', textAlign: 'center' }}>
        <TextField
          variant="outlined"
          label="Search"
          value={searchTerm}
          onChange={handleSearchChange}
          style={{ marginRight: '10px' }}
        />
        <Button variant="contained" color="primary">Search</Button>
      </div>
      <Grid container spacing={3}>
        {filteredNews.map((article, index) => (
          <Grid item xs={12} sm={6} md={4} key={index}>
            <Card onClick={() => handleArticleClick(article.url)} style={{ cursor: 'pointer' }}>
              <CardMedia
                component="img"
                height="140"
                image={article.urlToImage}
                alt={article.title}
              />
              <CardContent>
                <Typography variant="h6" component="h2" gutterBottom>{article.title}</Typography>
                <Typography variant="body2" color="textSecondary">{article.description}</Typography>
                <Typography variant="caption" color="textSecondary">{article.source.name}</Typography>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
}

export default App;
